.menu {
  @include onTablet {
    background: $color-whitest;
    height: 100vh;
    z-index: 3;
    order: 1;
  }
  
  &__list {
    display: flex;
    list-style: none;

    @include onTablet {
      margin-top: 0;
      padding: 0;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      background-color: $color-whitest;
      height: 100vh;
      z-index: 3;
      order: 1;
    }    
  }

  &__icon--close {
    display: none;

    @include onTablet {
      display: block;
      width: 24px;
      height: 24px;
      background-color: transparent;
    }
  }

  &__item {
    margin-left: 30px;
    @include onTablet {
      margin-bottom: 40px;
    }
  }

  &__link {
    text-decoration: none;
    text-transform: uppercase;
    color: $color-white;
    font-size: $normalFontSize;
    font-weight: $fontWeightNormal;
    transition: 0.3s;
    padding-bottom: 15px;

    @include onTablet {
      color: $color-black;
      font-weight: $fontWeightBold;
    }

    &--first,
    &:hover {
      color: $color-orange;
      text-decoration: none;
      border-bottom: 2px solid $color-orange;
    }
  }
}
